










import { Component, Vue, Prop, Emit, Watch } from 'vue-property-decorator';

@Component({
  name: 'sort-button'
})

export default class SortButton extends Vue {
  @Prop({
    required: true
  })
  title !: string;

  @Prop({
    required: false
  })
  reset !: boolean;

  @Prop({
    default: true
  })
  readonly threeType !: boolean;

  // -1 = 从小到大，1 = 从大到小， (0 = 默认)。
  status = 0;

  mounted() {
    if (this.threeType) {
      this.status = 0;
    } else {
      this.status = 1;
    }
  }

  @Emit()
  statusChange() {
    if (this.threeType) {
      this.status = this.status < 1 ? this.status + 1 : - 1;
    } else {
      this.status = -this.status;
    }
    return this.status;
  }

  @Watch('reset')
  resetBtn(val: boolean, oldVal: boolean) {
    const defaultValue = this.threeType ? 0 : 1;
    this.status = val ? defaultValue : this.status;
  }
}
